<script setup>
import { ref, onMounted, onUnmounted } from "vue";
import News from "@/components/LatestUpdates.vue";
import StudyDirection from "@/components/StudyDirection.vue";
import Treatise from "@/components/Treatise.vue";
import StudentInfor from "@/components/StudentInformation.vue";
import ScientificTeam from "@/components/ScientificTeam.vue";
import Ask from "@/components/Ask.vue";
import yaoqiu2 from "@/components/yaoqiu2.vue";

// ===== 新增：加载状态 =====
const loading = ref(true);

// 原有逻辑
const tabsList = [
  { label: "最新动态", name: "0" },
  { label: "个人信息", name: "1" },
  { label: "论文著作", name: "2" },
  { label: "科研项目", name: "3" },
  { label: "核心团队", name: "5" },
  { label: "招生信息", name: "6" },
];

const activeName = ref("1");
const isMobile = ref(false);

const handleClick = (tab) => {
  activeName.value = tab.props.name;
};

const throttle = (func, delay) => {
  let timeoutId;
  return (...args) => {
    if (!timeoutId) {
      timeoutId = setTimeout(() => {
        func(...args);
        timeoutId = null;
      }, delay);
    }
  };
};

const checkScreenSize = () => {
  isMobile.value = window.innerWidth <= 768;
};

const throttledCheck = throttle(checkScreenSize, 150);

onMounted(() => {
  // 模拟加载（可替换为真实 API 请求）
  setTimeout(() => {
    loading.value = false;
  }, 1200); // 1.2秒后显示页面

  checkScreenSize();
  window.addEventListener("resize", throttledCheck);
});

onUnmounted(() => {
  window.removeEventListener("resize", throttledCheck);
});
</script>

